body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.layflex {
    height: 100vh;
}
.header{
    display: flex;
}
.header img{
    padding: 10px;
    width: 64px;
}
.logo {
    color: white;
    font-size: 1.5em;
}

.charts-flex {
    text-align: center;
    width: 100%;
    margin: 10px;
    height: 400px;
    padding-right: 20px;
    padding-bottom: 40px;
}

.info-flex > p {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.info-flex {
    padding: 25px;
    background-color: white;
    box-shadow: 0 .5em 1em -.125em rgba(10, 10, 10, .1), 0 0 0 1px rgba(10, 10, 10, .02);
}

.sub-flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 15px;
}

.info-span {
    font-weight: bold;
}

.container {
    margin: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
}

.test-name {
    color: blueviolet;
}

.login-page {
    display: flex;
    justify-content: center;
    padding: 100px;
}

.login-container {
    display: flex;
    flex-direction: column;
}

.card-form {
    padding: 50px;
    text-align: center;
    flex-direction: column;

}

.card-form img {
    width: 250px;
    padding: 50px;
}

.ant-card {
    box-shadow: 0 .5em 1em -.125em rgba(10, 10, 10, .1), 0 0 0 1px rgba(10, 10, 10, .02);
}


.shadow-flex{
    box-shadow: 0 .5em 1em -.125em rgba(10, 10, 10, .1), 0 0 0 1px rgba(10, 10, 10, .02);

}

